		overflow-x: hidden;
	}
}
.service {
	&__box {
		display: flex !important;
		flex-direction: column;
		align-items: center;
		padding: 4em 6em;
		text-align: center;
		height: auto !important;
		justify-content: space-evenly;
		gap: 2em !important;
		&-image {
			background: #ffb800;
			padding: 3.5em;
			// padding: 4em;
			border-radius: 50%;
			margin-bottom: 2em;
			& img {
				height: 35px;
				// height: 50px;
				width: auto;
			}
		}
		&-heading {
			font-size: clamp(1.8rem, 1.5586rem + 1.3793vw, 2.8rem);
			font-weight: 700;
		}
		&-text {
			font-size: clamp(1.6rem, 1.4667rem + 0.3704vw, 1.8rem);
			font-weight: 300;
			color: #1d1d1d;
			letter-spacing: -0.5px;
		}
		&-button {
			font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
			padding: 0.6em 1.2em;
			margin-top: 3em;
			font-weight: 600;
			color: #fff;
			background-color: #1a2024;
			border-radius: 13px;
		}
	}
}
