@use "../base/colors" as *;
.navbar {
	width: 100%;
	position: fixed;
	border-bottom: 1px solid rgba(217, 217, 217, 0.15);
	z-index: 300000;
	display: none;
	align-items: center;
	justify-content: center;
    transition: background-color .3s, border-bottom .3s;
    &-shadow{
        background-color: #131618;
        border-bottom: 1px solid transparent;
    }

}
.logo {
	max-height: 15px;
	width: auto;
}
.navbar__nav {
	display: flex;
	flex-direction: row-reverse;
	align-items: center;
    justify-content: space-between;
	padding: 4em 0;
}
.navbar__list {
	display: none;
	width: 100%;
}
.navbar__list li {
	text-decoration: none;
	list-style-type: none;
}
.navbar__list li a {
	margin: 0.5em;
	text-decoration: none;
}
.navbar__item {
	& a {
		font-size: clamp(1.8rem, 1.6552rem + 0.8276vw, 2.2rem);
		font-weight: 200;
		color: #fff;
	}
}

.burger {
	display: none;
	flex-direction: column;
	background: transparent;
	border: none;
	outline: none;
	cursor: pointer;
}
.burger:focus .line {
	width: 3em;
	background-color: #5443ef;
}
