@charset "UTF-8";
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
}

main {
  overflow-x: clip !important;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1500px;
  padding: 0em 3em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 600px;
  background-image: url("../../dist/img/header_small.png");
  background-size: cover;
  background-position: center;
  z-index: 1;
  overflow: hidden;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 23, 25, 0.98);
  z-index: -1;
}
.header__box-heading {
  line-height: 1.3;
  color: #edf2ff;
  font-size: clamp(1.8rem, 0.6579rem + 6.5263vw, 8rem);
}
.header__box-heading span {
  display: inline-block;
  position: relative;
  width: max-content;
  padding: 0.3em 0.2em;
  z-index: 1;
}
.header__box-heading span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  animation: xd 1s forwards;
  height: 60%;
  transform: translate(0, -50%);
  border-radius: 17px;
  background-color: #fdb802;
  z-index: -1;
}
.header__box-text {
  font-size: clamp(1.6rem, 1.2308rem + 1.0256vw, 2rem);
  color: #dcdcdc;
  font-weight: 200;
}
.header__buttons {
  display: flex;
  gap: 3em;
  justify-content: center;
  margin-top: 5em;
  flex-direction: column;
}
.header__buttons-button {
  text-align: center;
  padding: 0.5em 3em;
  color: #fff;
  font-size: clamp(1.8rem, 0.8769rem + 2.5641vw, 2.4rem);
  border-radius: 15px;
  font-weight: 600;
}
.header__buttons-button--first {
  background-color: #fdb802;
}
.header__buttons-button--second {
  border: 2px solid #fff;
}
.header__arrow {
  position: absolute;
  bottom: 10em;
  z-index: 1;
  animation: upAndDown 1s infinite;
}
.header__arrow img {
  height: 40px;
}

@keyframes upAndDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes xd {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.navbar {
  width: 100%;
  position: fixed;
  border-bottom: 1px solid rgba(217, 217, 217, 0.15);
  z-index: 300000;
  display: none;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s, border-bottom 0.3s;
}
.navbar-shadow {
  background-color: #131618;
  border-bottom: 1px solid transparent;
}

.logo {
  max-height: 15px;
  width: auto;
}

.navbar__nav {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  padding: 4em 0;
}

.navbar__list {
  display: none;
  width: 100%;
}

.navbar__list li {
  text-decoration: none;
  list-style-type: none;
}

.navbar__list li a {
  margin: 0.5em;
  text-decoration: none;
}

.navbar__item a {
  font-size: clamp(1.8rem, 1.6552rem + 0.8276vw, 2.2rem);
  font-weight: 200;
  color: #fff;
}

.burger {
  display: none;
  flex-direction: column;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.burger:focus .line {
  width: 3em;
  background-color: #5443ef;
}

@media (max-width: 768px) {
  .our-services {
    overflow-x: hidden;
  }
}
.service__box {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  padding: 4em 6em;
  text-align: center;
  height: auto !important;
  justify-content: space-evenly;
  gap: 2em !important;
}
.service__box-image {
  background: #ffb800;
  padding: 3.5em;
  border-radius: 50%;
  margin-bottom: 2em;
}
.service__box-image img {
  height: 35px;
  width: auto;
}
.service__box-heading {
  font-size: clamp(1.8rem, 1.5586rem + 1.3793vw, 2.8rem);
  font-weight: 700;
}
.service__box-text {
  font-size: clamp(1.6rem, 1.4667rem + 0.3704vw, 1.8rem);
  font-weight: 300;
  color: #1d1d1d;
  letter-spacing: -0.5px;
}
.service__box-button {
  font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
  padding: 0.6em 1.2em;
  margin-top: 3em;
  font-weight: 600;
  color: #fff;
  background-color: #1a2024;
  border-radius: 13px;
}

.about {
  background-color: #131618;
}
.about__row {
  display: flex;
  gap: 4em;
  flex-direction: column;
}
.about__row-img {
  width: 100%;
  height: auto;
}
.about__row-img img {
  height: auto;
  width: 100%;
  object-fit: contain;
  max-height: 600px;
}
.about__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
  text-align: center;
  align-items: center;
  width: 100%;
}
.about__box-heading {
  font-size: clamp(1.6rem, 1.5517rem + 0.2759vw, 1.8rem);
  color: rgba(237, 243, 255, 0.4);
  font-weight: 300;
}
.about__box-text {
  font-size: clamp(1.7rem, 1.3862rem + 1.7931vw, 3rem);
  color: #fff;
  font-weight: 600;
}
.about__box-text--span {
  color: #FFB800;
}
.about__box-subtext {
  font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
  color: #fff;
  font-weight: 200;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}
.about__box-subtext-expand {
  font-size: clamp(1.7rem, 1.5rem + 0.5556vw, 2rem);
  color: #FFB800;
  border-radius: 14px;
  background-color: #1A2024;
  margin-top: 2vh;
  width: max-content;
  border: none;
  outline: none;
  padding: 0.6em 1.2em;
  cursor: pointer;
}
.about__box-subtext .expanded {
  white-space: normal;
  text-overflow: unset;
}

.section-padding {
  padding: 6vh 0;
}

.section__box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #131618;
}
.section__box-heading {
  font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
}
.section__box-text {
  font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
}

.swiper {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  clip-path: inset(0 -200px 0 0);
  overflow: hidden !important;
  margin-left: auto;
  padding: 2em 2em 2em 5em !important;
}
.swiper::before, .swiper::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20px; /* Szerokość gradientu */
  z-index: 10;
  pointer-events: none;
}
.swiper::before {
  left: -2px;
  background: linear-gradient(to right, rgb(255, 255, 255), rgba(255, 255, 255, 0));
}
.swiper-pagination {
  display: none;
  position: relative !important;
  justify-content: center;
  bottom: 0 !important;
  padding: 2em;
  align-items: center;
}
.swiper-pagination-bullet {
  transition: transform 0.1s;
}
.swiper-pagination-bullet-active {
  background-color: #ffb800 !important;
  transform: scale(1.5) !important;
}
.swiper-container {
  display: flex !important;
  flex-direction: column !important;
  overflow: visible !important;
  position: relative;
  max-width: 1500px;
  margin: 0 auto;
}
.swiper-button-prev, .swiper-button-next {
  display: none !important;
  border-radius: 50%;
  position: relative !important;
  margin: 5em 0;
  left: 0 !important;
  top: 0 !important;
  box-shadow: 0px 4px 3.7px rgba(0, 0, 0, 0.09);
}
.swiper-button-prev {
  background-color: #ffb800;
  color: #fff !important;
  padding: min(3vh + 0.8vw, 44px) !important;
}
.swiper-button-prev::after {
  content: "" !important;
}
.swiper-button-next {
  background-color: #fff;
  color: #ffb800 !important;
  padding: min(2vh + 0.8vw, 30px) !important;
}
.swiper-button-next::after {
  content: "" !important;
}
.swiper-slide {
  border-radius: 50px;
  height: auto !important;
  transition: opacity 0.5s ease;
  transition: transform 0.6s ease, opacity 0.5s ease;
  box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
}
.swiper-slide.active-slide {
  z-index: 1000;
}

.swiper-container {
  display: flex !important;
  flex-direction: row !important;
  justify-content: center;
  align-items: center;
}

.swiper-pagination {
  display: flex;
}

.swiper-buittons {
  position: absolute;
  left: -20em;
  top: 35%;
  transform: translate(0, -35%);
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000 !important;
}

.swiper-scrollbar {
  display: none;
}

@media (max-width: 992px) {
  .header__arrow {
    display: none;
  }
}
@media (min-width: 320px) and (max-width: 768px) {
  .header__arrow {
    display: none;
  }
  .swiper {
    padding: 5em 3em !important;
  }
  .swiper::after, .swiper::before {
    width: 0;
  }
}
@media (min-width: 576px) {
  .swiper {
    padding: 10em 2em 10em 5em !important;
  }
  .section-padding {
    padding: 6vh 0;
  }
  .section__box {
    text-align: left;
  }
  .service__box-image {
    padding: 4em;
  }
  .service__box-image img {
    height: 50px;
  }
}
@media (min-width: 768px) {
  .header__box {
    text-align: center;
  }
  .header__box-heading span {
    transform: rotate(-3deg);
  }
  .header__buttons {
    flex-direction: row;
  }
  .swiper-slide {
    transform: scale(0.92) !important;
  }
  .swiper-slide.active-slide {
    transform: scale(1.05) !important;
    z-index: 1000;
  }
}
@media (min-width: 992px) {
  .navbar {
    display: flex;
  }
  .navbar__list {
    display: flex;
  }
  .logo {
    max-height: 30px;
  }
  .header {
    background-image: url("../../dist/img/header_big.png");
  }
  .about__box {
    text-align: left;
    align-items: flex-start;
  }
  .about__box-heading {
    font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
    color: #fff;
    font-weight: 600;
  }
  .about__row {
    flex-direction: row;
  }
}
@media (min-width: 1200px) {
  .swiper-container {
    padding: 0 13em 0 10em;
  }
  .container {
    padding: 0 15em;
  }
}
@media (min-width: 1400px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: flex !important; /* Ukryj przyciski */
  }
  .swiper-buittons {
    display: flex;
  }
  .swiper-buittons {
    left: 5em;
  }
  .container {
    padding: 0 20em;
  }
  .swiper-pagination {
    display: none;
    margin-top: 5em;
  }
  .swiper-container {
    padding: 0 20em;
  }
}
@media (min-width: 1600px) {
  .swiper-pagination {
    display: none;
    margin-top: 5em;
  }
}
@media (min-width: 1800px) {
  .container {
    padding: 0 10em;
  }
  .swiper-container {
    padding: 0 10em;
  }
  .swiper-buittons {
    left: -5em;
  }
}